import { Component, OnInit } from '@angular/core';
import { ProductService, ProductModel } from '../products/product.service';
import { ProductsListComponent } from '../products/products-list.component';
import {ActivatedRoute} from '@angular/router';

@Component({
    moduleId: module.id,
    selector: 'admin',
    directives: [ProductsListComponent],
    providers: [ProductService],
    template: `
    <products-list [isAdmin]="true"></products-list>
        `
})
export class AdminComponent implements OnInit {
    private products;

    constructor(private route: ActivatedRoute,
        private productService: ProductService) { }

    ngOnInit() {
        const prmProducts = this.productService.query();

        prmProducts.then((products: ProductModel[]) => {
            this.products = products;
        });

        prmProducts.catch(err => {
            console.log('Sorry, cannot load the products, try again later');
        });
    }
    

}